.TodoSearch{
    background: #f9fbfc;
    border-radius: 20px;
    border: 2px solid #202329;
    margin: 0 24px;
    height: 64px;
    width: calc(50% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: monospace;
    font-weight: 400;
    color: #1e1e1f;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, .25); 
}

.TodoSearch::placeholder{
    color: #a5a5a5;
    font-family: monospace;
    font-weight: 400;
}

.TodoSearch:focus{
    outline-color: #61dafa;
}